*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  background: #BC3CB1;
}
button{
  background: #767676;
  font-size: 40;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
}
button:hover{
  background: #000;
}
button:active{
  background: red;
}

.App{
  margin: 50vh auto;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  max-width: 30%;
  display: -ms-flexbox;
  display: flex;
  height: 100px;
}
.plus{
  float: right;

}
.table{
  text-align: center;
  line-height: 100px;
  background: #46FF52;

}
button, .table{
  display: block;
  width: 25%;
  border: 3px solid #000;

}



